body {
  font-family: Arial, Helvetica, sans-serif;
  margin: 0px;
  padding: 0px;
  font-size: 16px;
  background-image: url('/images/right_im.jpg');
  background-repeat: repeat-y;
  background-attachment: scroll;
}
ul li {
  padding: 5px;
}
a:link, a:visited {
  color: black;
}
a:hover {
  color: blue;
}

#wrapper {
  background-color: #FFF;
  text-align: left;
  padding: 0px;
  height: auto;
  width: 1024px;
  margin-right: auto;
  margin-left: auto;
}
#login-status {
  margin: 0px;
  padding: 0px;
  float: right;
  font-size: 14px;
}
#header {
  background-color: #FFF;
  margin: 0px;
  margin-top: 25px;
  padding: 0px;
  height: 148px;
  background-image: url('/images/header.jpg');
}
#navigation {
  margin: 0px;
  padding: 0px;
  height: 34px;
}
#sidebar {
  background-color: #FFF;
  margin: 0px;
  padding: 0px;
  float: left;
  height: 234px;
  width: 439px;
  //background-image: url('/images/sidebar.jpg');
}
.listItem {
  font-size: 16px;
  font-weight: bold;
  margin-top: 25px;
}
.listStrong {
  color: #000;
}
.listLight {
  color: #666;
}
#content {
  margin: 0px;
  padding: 0px;
  float: left;
  width: 510px;
}
